import React from 'react';
import PropTypes from 'prop-types';
import { platform, IOS } from '@vkontakte/vkui';
import { Panel, PanelHeaderSimple, PanelHeaderButton, Button, List, Cell, Group, ScreenSpinner, Div, Avatar } from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

import bridge from '@vkontakte/vk-bridge';
import Api from '../core/api/Api';

import {getGroupName} from '../components/DangerMeter';

// Redux
import { connect } from 'react-redux';
//import { selectRegion } from '../redux/actions/region';

import './Rating.css';

const osName = platform();

class Rating extends React.Component {

	constructor(props) {
    super(props);
    this.state = {isLoading: true};
  };

	// Компонент примонтирован
	componentDidMount() {
		Api.rating.global((table) => {
			this.setState({isLoading: false, table: table});
		});
  };

	share() {
		bridge
			.send("VKWebAppShowStoryBox", {
				background_type: "image",
				url: "https://sun9-56.userapi.com/c855620/v855620203/21b0af/hX1H8hzH848.jpg",
				attachment: {
					text: 'open',
					type: 'url',
					url: 'https://vk.com/app7405679'
				}
			})
			.then(() => {alert('Yes')});
	}

	render() {
		return (
			<Panel id={this.props.id} separator={false}>
				<PanelHeaderSimple
					left={<PanelHeaderButton onClick={() => {this.props.navigate('level')}}>
						{osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
					</PanelHeaderButton>}
				>
					Рейтинг
				</PanelHeaderSimple>
				{!this.state.isLoading && <Group>
		      <List className="RatingList">
						<Cell expandable>
							<Div><Button onClick={()=>this.share()}>Опубликовать историю</Button></Div>
							<Div>Баллы</Div>
						</Cell>
						{this.state.table.map((friend, index) => {
              return (
								<Cell
									key={"friend_" + friend.uid}
									before={friend.photo_200 ? <Avatar src={friend.photo_200}/> : null}
									description={getGroupName(friend.group)}
								>
			        		<Div>
										{`${friend.first_name} ${friend.last_name}`}
									</Div>
									<Div>
										{friend.value}
									</Div>
								</Cell>
							);
						})}
					</List>
				</Group>}
				{this.state.isLoading && <ScreenSpinner size='large' />}
			</Panel>
		);
	}
}

const mapStateToProps = (state, ownProps) => {
  return {
			//friends: state.rating.friends
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
      /*onSelectRegion: (region) =>
        new Promise((resolve) =>
          selectRegion(region).then((action) => {
            dispatch(action);
            resolve();
          })
        )*/
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Rating);
